          >
            <v-list-item-content>
              <v-btn
                  :to="{ name: 'login' }"
                  color="accent"
                >Log in</v-btn>
            </v-list-item-content>
          </v-list-item>
          <v-list-item
            class="pa-0"
            :to="{ name: route.name }"
            v-for="route in routes" :key="route.name"
          >
            <v-list-item-avatar>
              <v-icon>{{ route.meta.icon }}</v-icon>
            </v-list-item-avatar>
            <v-list-item-content>{{ capitalize(route.name) }}</v-list-item-content>
