<script>
import { mapState } from 'vuex';
import onecolor from 'onecolor';
import { capitalize } from '../lib/utils';

export default {
  computed: {
    routes() {
      return this.$router.getRoutes().filter(r => r.meta.display)
    },
    ...mapState([
      'headerColorHex',
      'lightThreshold',
      'loggedIn',
      'cognitoInfo',
      'loadingState'
    ]),
    dark() {
      return onecolor(this.headerColorHex || 'f00').lightness() < this.lightThreshold;
    },
    iconSize() {
      return ['xs', 'sm'].includes(this.$vuetify.breakpoint.name) ? '48px' : '56px'
    },
    iconMargin() {
      return ['xs', 'sm'].includes(this.$vuetify.breakpoint.name) ? '8px' : '4px'
    },
  },
  data: () => ({
    drawer: false,
  }),
  methods: {
    onecolor,
    capitalize,
    // login() {
    //   this.$auth.loginWithRedirect();
    // },
    // // Log the user out
    // logout() {
    //   this.$auth.logout({
    //     returnTo: window.location.href
    //   });
