import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    count: 0,
    headerColorHex: '',
    lightThreshold: 0.5,
    activePalette: null,
    loggedIn: false,
    cognitoInfo: null,
    errorLoadingState: false,
    loadingState: false,
  },
  mutations: {
    increment (state) {
      state.count++
    },
    setHeaderColorHex(state, hex) {
      state.headerColorHex = hex;
    },
    setActivePalette(state, palette) {
      state.activePalette = palette;
    },
    loggedIn(state, v) {
      state.loggedIn = v;
    },
    cognitoInfo(state, v) {
      state.cognitoInfo = v;
    }
  },
  actions: {
    setHeaderColorHex({ commit }, hex) {
      commit('setHeaderColorHex', hex)
    },
    setActivePalette({ commit }, palette) {
      commit('setActivePalette', palette);
    },
    setLoggedIn({ commit }, newValue) {
      commit('loggedIn', newValue)
    },
    setLoggedOut({ commit }) {
      commit('loggedIn', false);
      commit('cognitoInfo', {});
    },
    setCognitoInfo({ commit }, newValue){
      commit('cognitoInfo', newValue)
    },
  }
})

export default store;
