<template>
  <BaseView>
    <template v-slot:main>
      <v-container fluid class="fill-height pa-0">
        <PaletteViewComponent
          :colors="selected"
        ></PaletteViewComponent>
        <v-navigation-drawer
          app
          width="500px"
          v-model="rightDrawer"
          color="primary darken-2"
          right
        >
          <div class="text-right">
            <v-btn
              icon
              @click="randomize"
              :dark="dark"
              tooltip="Randomize Colours"
              :style="{ width: iconSize, height: iconSize, margin: '4px' }"
            ><v-icon>mdi-refresh</v-icon></v-btn>
            <v-btn
              :style="{ width: iconSize, height: iconSize, margin: '4px' }"
              @click="rightDrawer = !rightDrawer"
              :dark="dark"
              icon
            >
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </div>
          <v-card :elevation="0" style="margin-bottom: 100px;" color="primary darken-2">
            <!-- Provides the application the proper gutter -->
            <v-card
              v-for="type of types" :key="type"
              :dark="onecolor(selected[type].hex).lightness() < lightThreshold"
              tile
              :style="{ backgroundColor: selected[type].hex }"
              class="pa-4"
              background-color="none"
            >
              <v-card-title>{{ capitalize(type) }}</v-card-title>
              <v-card-text
                class="pa-4"
              >
                <ColorAutocompleteComponent
                  :colors="colors"
                  v-model="selected[type]"
                  @change="colorUpdate"
                ></ColorAutocompleteComponent>
              </v-card-text>
            </v-card>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="accent"
                :dark="dark"
                @click="save"
                :disabled="!canSave"
              >Save</v-btn>
            </v-card-actions>
            <!-- <v-divider class="my-8 mx-4"></v-divider>
            <div class="headline text-center">Saved Palettes</div>
            <v-divider class="my-8 mx-4"></v-divider>
            <v-list-item v-for="sp in savedPalettes" @click="selected = sp" :key="sp.key">
              <v-list-item-avatar class="sp">
                <div class="sp--base" :style="{ backgroundColor: sp.base.hex }"></div>
                <div class="sp--white" :style="{ backgroundColor: sp.white.hex }"></div>
                <div class="sp--accent" :style="{ backgroundColor: sp.accent.hex }"></div>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>{{ sp.key }}</v-list-item-title>
              </v-list-item-content>
              <v-list-item-action>
                <v-btn icon @click="remove(sp)">
                  <v-icon color="red lighten-1">mdi-delete</v-icon>
                </v-btn>
              </v-list-item-action>
            </v-list-item> -->
          </v-card>
        </v-navigation-drawer>
      </v-container>
    </template>
    <template v-slot:app-bar-right>
      <v-btn icon @click="rightDrawer = !rightDrawer"><v-icon>mdi-palette</v-icon></v-btn>
    </template>
  </BaseView>
</template>

<script>
import { capitalize } from '../lib/utils';
import onecolor from 'onecolor';
import colors from '../colors.json';
import ColorAutocompleteComponent from '../components/ColorAutocompleteComponent.vue';
import PaletteViewComponent from '../components/PaletteViewComponent.vue';
import BaseView from './BaseView.vue';
import { mapActions, mapState } from 'vuex';

export default {
  name: 'HomeView',
  components: {
    ColorAutocompleteComponent,
    PaletteViewComponent,
    BaseView
  },
  created() {
    this.colors.sort((a,b) => a.name > b.name ? 1 : -1);

    this.byFam = this.colors.reduce((acc, c) => {
      acc[c.fam] = acc[c.fam] || []
      acc[c.fam].push(c);
      return acc;
    }, {});

    try {
      if (localStorage.getItem('sp')) {
        this.savedPalettes = JSON.parse(localStorage.getItem('sp'));
        console.log(this.savedPalettes)
      }
    } catch(e) {
      console.error(e);
    }

    const { base, white, accent } = this.$route.query;
    if (base && white && accent) {
      this.selected.base = this.colors.find(c => c.name == base);
      this.selected.white = this.colors.find(c => c.name == white);
      this.selected.accent = this.colors.find(c => c.name == accent);
    } else {
      if (this.savedPalettes?.length) {
        this.selected = this.savedPalettes[0];
      } else if (this.activePalette) {
        this.selected = JSON.parse(JSON.stringify(this.activePalette))
      } else {
        this.randomize();
      }
    }
  },
  data: () => ({
    rightDrawer: false,
    types: [
      'base',
      'white',
      'accent',
    ],
    selected: {
      base: {
        hex: '',
        name: '',
      },
      white: {
        hex: '',
        name: '',
      },
      accent: {
        hex: '',
        name: '',
      },
    },
    lightThreshold: 0.5,
    savedPalettes: [],
    colorFamilies: [
      'red',
      'orange',
      'yellow',
      'green',
      'blue',
      'purple',
      'gray',
      'neutral',
      'white',
    ],
    colors,
  }),
  methods: {
    ...mapActions([
      'setHeaderColorHex',
      'setActivePalette',
    ]),
    capitalize,
    onecolor,
    randomize() {
      do {
        this.selected['base'] = this.colors[Math.floor(Math.random() * this.colors.length)];
      } while(onecolor(this.selected['base'].hex).lightness() > 0.5)
      do {
        this.selected['accent'] = this.byFam['red'][Math.floor(Math.random() * this.byFam['red'].length)];
      } while(onecolor(this.selected['accent'].hex).lightness() > 0.5)

      this.selected['white'] = this.byFam['white'][Math.floor(Math.random() * this.byFam['white'].length)];
    },
    save() {
      const palette = JSON.parse(JSON.stringify(this.selected));
      palette.key = this.getKey(palette);
      this.savedPalettes.push(palette);
      localStorage.setItem('sp', JSON.stringify(this.savedPalettes));
    },
    remove(item) {
      this.savedPalettes.splice(this.savedPalettes.indexOf(item), 1);
      localStorage.setItem('sp', JSON.stringify(this.savedPalettes));
    },
    getKey(palette) {
      return Object.entries(palette).map(([, v]) => v.name).join('/');
    },
    colorUpdate() {
      // const query = {
      //   base: this.selected.base?.name,
      //   white: this.selected.white?.name,
      //   accent: this.selected.accent?.name,
      // };

      // this.$router.replace({ path: this.$route.path, query});

      const theme = {
        primary: this.selected.base?.hex,
        secondary: this.selected.white?.hex,
        accent: this.selected.accent?.hex
      };

      console.log(theme)

      this.$vuetify.theme.themes.light = { ...this.$vuetify.theme.themes.light, ...theme };
      this.$vuetify.theme.themes.dark = { ...this.$vuetify.theme.themes.dark, ...theme };
      this.setActivePalette(this.selected);
    },
  },
  computed: {
    ...mapState([
      'headerColorHex',
      'activePalette',
    ]),
    canSave() {
      const keyToFind = this.getKey(this.selected);
      const found = this.savedPalettes.find(i => i.key == keyToFind)
      const canSave = found == null;
      return canSave;
    },
    headerColor() {
      const color = onecolor(this.selected['base'].hex || '#f00');
      return color.lightness(color.lightness() * 0.5);
    },
    dark() {
      return this.headerColor.lightness() < this.lightThreshold;
    },
    iconSize() {
      return ['xs', 'sm'].includes(this.$vuetify.breakpoint.name) ? '48px' : '56px'
    },
  },
  watch: {
    headerColor() {
      this.setHeaderColorHex(this.headerColor.hex());
    },
    selected() {
      console.log('color update')
      this.colorUpdate();
    }
  }
}
</script>
